/* App.css */
/* App.css */
.App {
  /* display: flex; */
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
}

.dashboard {
  flex: 1;
  margin-right: 20px;
}

.images-dashboard {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.comic-form {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

textarea {
  width: 50%; /* Adjust textarea width as needed */
  height: 50px; /* Increase textarea height */
  padding: 10px;
  margin-bottom: 10px;
  min-width: 280px;
}

/* Rest of your CSS remains the same */

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

.comic-strip {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.comic-panel > img{
  border: 2px solid #ccc; /* Increase border size */
  padding: 20px; /* Increase padding */
  margin: 50px; /* Increase margin */
  width: 250px; /* Increase width */
  text-align: center;
}

.error-message {
  color: red;
  margin-top: 20px;
}
